.title{
    color: darkred;
    font-size: 4rem;
    font-weight: bold;
    letter-spacing: 3px;
    padding: 25px;
    text-shadow: 3px 4px 5px #ff7000;
}

.input{
    background-color: bisque;
    border-color: darkred;
    border-radius: 10px;
    margin-left: 80px;
    margin-bottom: 55px;
    padding: 12px 10px 12px 10px;
}
.input::placeholder{
    color: darkred;
}

.btn{
    border: darkred;
    color: darkred ;
    background-color: bisque ;
    box-shadow: 0 0 16px 0 #ff7000, 0 6px 20px 0 #ff7000;
    border-radius: 20px;
    cursor: pointer;
    margin-left: 10px;
    padding: 10px 15px 10px 15px;
}

.section{
    display: flex;
    justify-content: space-around;
}

.list{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
}

.favorites{
    display: flex;
    flex-direction: column;
    align-items: center;

.favorites__title{
    color: darkred;
    text-shadow: 3px 1px 5px #ff7000;
    font-size: 3.5rem;
    }
}

.favorites__list{
    margin-top: 30px;
}

.img{
    height: 140px;
    width: 130px;
}